.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.headlogo{
  width: 100%;
  height: 68px;
}

.section{
  background-color:rgb(59, 121, 68);
  height: 500px;
  max-width: 100%;
}
.text-center{
  padding-top: 180px;
}
 .contentvazir{
  font-size: 60px;
 }
 .container{
  padding-top: 90px;
 }
